<template>
  <div class="wrap-g">
    <mt-header title="我的收藏" style="background: #fff;color: #1B1B1B;">
      <router-link to="/my" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <div class="flex">
      <div class="flex1" v-for="(item,index) in list" :class="activeName==item.path?'active':''" @click="handleClick(item)">
        <span>{{item.txt}}</span>
      </div>
    </div>
    <div class="main">
      <transition name="fade" mode="in-out">
        <router-view ></router-view>
      </transition>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
//      {path: '/allorder', name: '全部订单', component: resolve => require(["../components/my/myorder/allorder.vue"],resolve)},
//      {path: '/unpaidorder', name: '待付款', component: resolve => require(["../components/my/myorder/unpaidorder.vue"],resolve)},
//      {path: '/appraisedorder', name: '待评价', component: resolve => require(["../components/my/myorder/appraisedorder.vue"],resolve)},
//      {path: '/saleorder', name: '销售订单', component: resolve => require(["../components/my/myorder/saleorder.vue"],resolve)},
//      {path: '/presaleorder', name: '预售订单', component: resolve => require(["../components/my/myorder/presaleorder.vue"],resolve)},
        list:[
          {
            txt:"全部订单",
            path:"/allorder"
          },
          {
            txt:"待付款",
            path:"/unpaidorder"
          },
          {
            txt:"待评价",
            path:"/appraisedorder"
          },
          {
            txt:"销售订单",
            path:"/saleorder"
          },          {
            txt:"预售订单",
            path:"/presaleorder"
          },

        ],
        activeName:"/allorder",
      }
    },
    methods:{
      handleClick(e){
        this.activeName=e.path
        this.$router.push({ path: e.path});
      },

    },
    mounted(){
      this.activeName=this.$route.path;
    },
  }
</script>


<style lang="less" scoped>
  .flex{
    text-align: center;
    .flex1{
      line-height: 40px;
      color: #1B1B1B;
      span{
        position: relative;
      }
    }
    .flex1.active span{
      color: #00CA02;
    }
    .flex1.active span:before{
      content:'';
      position: absolute;
      bottom: -5px;
      width: 80%;
      left: 10%;
      height: 2px;
      background: #00CA02;
    }
  }
</style>



